<template>
  <div class="platePick">
    <div class="platePick-title">{{ $route.meta.platePickData.platePickInfo }}</div>
    <div class="plateCheck-body">
      <el-row>
        <el-col class="plate-flex-row" :span="24">
            <div class="plateStatus plateStart">
                <el-button type="primary" ref="doPlate" @click="doPlate">开始选号</el-button>
            </div>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <div class="plateStatus plateDone">
              <div class="plateMake-button">
                <el-button type="success" ref="makePlate" @click.once="plateMake">确认号牌</el-button>
              </div>
              <div class="plateCurrent">当前选择的号牌: {{ plateChecked }}</div>
          </div>
          
        </el-col>
      </el-row>
    </div>
    <div class="plateLayout">
      <el-checkbox-group @change="platePickChange" size="medium">
        <div class="plateCheckbox" v-for="item in plateDatas" :key="item">
          <el-checkbox-button ref="checkbox" disabled :label="item" :key="item">{{ item }}</el-checkbox-button>
        </div>
      </el-checkbox-group>
    </div>
  </div>
</template>
<script>
import platePick from "./data/plate.json";
import { getPlate } from '@/api/user'
export default {
  name: "PlatePick",
  data() {
    return {
      plateChecked: "",
      plateDatas: []
    };
  },
  mounted() {
    getPlate().then(resp => {
      console.log(resp)
    })
    this.plateDatas = platePick.plates;
    sessionStorage.setItem('plateData', JSON.stringify(this.$route.params))
  },
  methods: {
    plateMake() {
        this.$refs['doPlate'].$off('click')
        setTimeout(() => {
            this.$router.push({ name: 'plateMake', params: { plate: this.plateChecked } })
            sessionStorage.setItem('plate', this.plateChecked)
        }, 3000)
    },
    doPlate() {
      let start = 0;
      let random = Math.floor(Math.random() * (60 - 30)) + 15;
      let highNum;
      let len = this.$refs["checkbox"].length;
      let timer = setInterval(() => {
        start++;
        if (start > random) {
          clearInterval(timer);
          const plate = this.$refs["checkbox"][start + highNum - 1].$el.innerText;
          this.plateChecked = [];
          this.plateChecked = plate;
          for (let i = 0; i < len; i++) {
            this.$refs["checkbox"][i].$el.classList.remove("is-disabled");
            this.$refs["checkbox"][i].$el.classList.remove("is-checked");
            this.$refs["checkbox"][i].$el.classList.add("is-disabled");
          }
          this.$refs["checkbox"][start + highNum - 1].$el.classList.remove("is-disabled");
          this.$refs["checkbox"][start + highNum - 1].$el.classList.add("is-checked");
        } else {
          highNum = Math.floor(Math.random() * 15);
          for (let i = 0; i < len; i++) {
            this.$refs["checkbox"][i].$el.classList.add("is-disabled");
          }
          this.$refs["checkbox"][start + highNum].$el.classList.remove("is-disabled");
          this.$refs["checkbox"][start + highNum].$el.classList.add("is-checked");
        }
      }, 180);
    },
    platePickChange(val) {
      console.log(val);
    }
  }
};
</script>
<style lang="scss">
.platePick {
   .plateCheck-body {
        display: flex;
        width: 70%;
        margin: 0 auto;
        flex-direction: column;
        .plateStatus {
            text-align: left;
            height: 36px;
            overflow: hidden;
            margin: 18px 0;
            .plateMake-button {
                float: left;
            }
            .plateCurrent {
                margin-left: 100px;
                text-align: center;
                height: 36px;
                line-height: 36px;
            }
        }
   }
  .platePick-title {
    font-weight: bold;
    padding: 16px 0;
  }
  .plateLayout {
    display: flex;
    flex-wrap: wrap;
    width: 70%;
    margin: 0 auto;
    .el-checkbox-group {
      display: flex;
      flex-wrap: wrap;
      .el-checkbox-button__inner {
          display: inline-block;
          width: 100%;
      }
      .el-checkbox-button {
          width: 100%;
      }
      .plateCheckbox {
        width: 120px;
        text-align: left;
        margin: 8px 6px;
      }
    }
  }
}
</style>